@import url(https://fonts.googleapis.com/css?family=Montez|Sofia);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Harry Potter styling */

body {
  font-family: "Sofia", cursive;
  height: 100%;
  width: 100%;
}

.App {
  height: 100%;
  width: 100%;
}

.quotation {
  font-size: xx-large;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
  font-size: x-large;
}

#logo {
  height: 10%;
  width: 100%;
}

/* Centered text */

.centered {
  position: absolute;
  width: 55%;
  top: 60%;
  left: 51%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
  font-size: 3em;
  text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
}

.author {
  font-size: 1em;
  color: #fff;
  text-align: center;
  -webkit-animation: author 1s ease-in-out infinite alternate;
  animation: author 1s ease-in-out infinite alternate;
}

@-webkit-keyframes author {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #e60073,
      0 0 40px #e60073, 0 0 50px #e60073, 0 0 60px #e60073, 0 0 70px #e60073;
  }

  to {
    text-shadow: 0 0 20px #fff, 0 0 30px #ff4da6, 0 0 40px #ff4da6,
      0 0 50px #ff4da6, 0 0 60px #ff4da6, 0 0 70px #ff4da6, 0 0 80px #ff4da6;
  }
}

@keyframes author {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #e60073,
      0 0 40px #e60073, 0 0 50px #e60073, 0 0 60px #e60073, 0 0 70px #e60073;
  }

  to {
    text-shadow: 0 0 20px #fff, 0 0 30px #ff4da6, 0 0 40px #ff4da6,
      0 0 50px #ff4da6, 0 0 60px #ff4da6, 0 0 70px #ff4da6, 0 0 80px #ff4da6;
  }
}

img {
  height: 100%;
  width: 100%;
}

h1 {
  text-align: center;
  font-size: 3em;
}

h3 {
  font-size: 2em;
  text-align: center;
}

p {
  font-size: 20px;
  font-family: "Montez", cursive;
}

ul {
  text-align: center;
  font-size: 30px;
  list-style-type: none;
}

.harry-books {
  text-align: center;
}

.harry-books img {
  height: 15em;
  width: 20em;
}

.container > p {
  font-size: 1.3em;
  text-align: center;
  margin: 10px;
}

.hp-glass {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  height: 175px;
  width: 150px;
}

.card {
  height: 400px;
  width: 200px;
  box-shadow: 0 4px 10px 0 rgb(191, 63, 63);
  transition: 0.3s;
}

.avatarDetails {
  text-align: center;
}

.avatarImg {
  border-radius: 50px;
  height: 200px;
}

.card:hover {
  opacity: 5;
}

.flex-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  background-image: url("https://images.ctfassets.net/usf1vwtuqyxm/5nViubjcRMMc9U1hsgWHXf/20cdbf8454d516804d9cae2208018289/MinaLima_Black_Family_Tapestry_wallpaper_roll.jpg");
  background-attachment: fixed;
  padding: 1%;
}

.flex-container > div {
  background-color: #f1f1f1;
  background-repeat: no-repeat;
  background-position-x: -3.9em;
  background-position-y: -0.9em;
  margin: 1em;
  padding: 2.2em;
  opacity: 0.5;
  margin-bottom: 50px;
  margin-top: 50px;
  height: 250px;
}

.footer {
  text-align: center;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .mobileView {
    display: none;
  }

  .flex-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    background-image: url("https://images.ctfassets.net/usf1vwtuqyxm/5nViubjcRMMc9U1hsgWHXf/20cdbf8454d516804d9cae2208018289/MinaLima_Black_Family_Tapestry_wallpaper_roll.jpg");
    background-attachment: fixed;
    padding: 1%;
  }
  
  .flex-container > div {
    background-color: #f1f1f1;
    background-repeat: no-repeat;
    background-position-x: -3.9em;
    background-position-y: -0.9em;
    opacity: 0.5;
    height: 250px;
    width: 120px;
  }
}
/**
 * card shine effect
 */

.card {
  position: relative;
  overflow: hidden;
  -webkit-filter: blur(2px);
          filter: blur(2px);
}

/**
   * The "shine" element
   */

.card:after {
  content: "";
  position: absolute;
  top: -110%;
  left: -210%;
  width: 200%;
  height: 200%;
  opacity: 0;
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);

  background: rgba(255, 255, 255, 0.13);
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0.13) 0%,
    rgba(255, 255, 255, 0.13) 77%,
    rgba(255, 255, 255, 0.5) 92%,
    rgba(255, 255, 255, 0) 100%
  );
}

/* Hover state - trigger effect */

.card:hover {
  -webkit-filter: blur(0px);
          filter: blur(0px); /* IE 9 */
  -webkit-transform: scale(1.2); /* Safari 3-8 */
  transform: scale(1.2);
}

.card:hover:after {
  opacity: 1;
  top: -30%;
  left: -30%;
  transition-property: left, top, opacity;
  transition-duration: 0.7s, 0.7s, 0.15s;
  transition-timing-function: ease;
}

/* Harry Potter image Floating style */

.harry-floating img {
  position: absolute;
  width: 30em;
  height: 30em;
  overflow: hidden;
  -webkit-transform: translatey(0px);
          transform: translatey(0px);
  -webkit-animation: float 6s ease-in-out infinite;
          animation: float 6s ease-in-out infinite;
}

.harry-potter {
  top: 55px;
  left: 25px;
  z-index: 3;
}

/* Harry Potter logo Floating style */

.harry-potter-title {
  display: flex;
  align-items: center;
  justify-content: center;
}
.harry-potter-title-wrap {
  width: 100%; /*or whatever you choose*/
  margin: auto;
  z-index: 2;
}
.harry-potter-title-wrap img {
  top: 10px;
  left: 0;
  right: 0;
  position: absolute;
  overflow: hidden;
  -webkit-transform: translatey(0px);
          transform: translatey(0px);
  -webkit-animation: float 5s ease-in-out infinite;
          animation: float 5s ease-in-out infinite;
  transition: all 0.9s ease-in-out;
  width: 100%;
  width: 50em;
  height: 10em;
  height: auto; /* maintain aspect ratio*/
  margin: auto; /*optional centering of image*/
}

@-webkit-keyframes float {
  0% {
    /* box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6); */
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
  50% {
    /* box-shadow: 0 25px 15px 0px rgba(0, 0, 0, 0.2); */
    -webkit-transform: translatey(-20px);
            transform: translatey(-20px);
  }
  100% {
    /* box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6); */
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
}

@keyframes float {
  0% {
    /* box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6); */
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
  50% {
    /* box-shadow: 0 25px 15px 0px rgba(0, 0, 0, 0.2); */
    -webkit-transform: translatey(-20px);
            transform: translatey(-20px);
  }
  100% {
    /* box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6); */
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
}

