.ag-lightning {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.ag-spark:before {
  -webkit-animation: an-spark 1800s linear infinite,
    an-slow-shake 26s linear 13s infinite;
  animation: an-spark 1800s linear infinite,
    an-slow-shake 26s linear 13s infinite;
}
.ag-spark:after {
  -webkit-animation: an-spark 3600s linear infinite,
    an-slow-shake-revert 26s linear infinite;
  animation: an-spark 3600s linear infinite,
    an-slow-shake-revert 26s linear infinite;
}

.ag-lightning:before,
.ag-lightning:after {
  content: "";

  opacity: 0;

  will-change: transform;

  position: absolute;
}
.ag-lightning:before {
  width: 272px;
  background-image: url(https://raw.githubusercontent.com/SochavaAG/example-mycode/master/pens/sparks/images/lightning-1.png);

  top: -9.75em;
  left: 50%;

  -webkit-animation: an-lightning-first 30s linear 3s infinite;
  animation: an-lightning-first 30s linear 3s infinite;
}
.ag-lightning:after {
  width: 254px;
  background-image: url(https://raw.githubusercontent.com/SochavaAG/example-mycode/master/pens/sparks/images/lightning-2.png);

  top: -10.1875em;
  right: -6.1875em;

  -webkit-animation: an-lightning-second 30s linear 3s infinite;
  animation: an-lightning-second 30s linear 3s infinite;
}

